        />
      </div>
      <div class="col col-md-2">
        <button
          type="submit"
          class="btn btn-primary btn-block"
          @click="searchTable"
        >
          Search
        </button>
      </div>
    </div>
    <small id="searchHelp" class="form-text text-muted"
      >Enter phrase and hit enter to add phrase to list of searched words</small
    >
  </div>
</template>

<script>
